.Hero {
  font-family: 'Migra Extra Bold';
  text-align: center;
  font-size: 5em;
  user-select: none;
  letter-spacing: 5px; 
}

@media (max-width: 1024px) {
  .Hero {
      font-size: 2.5em;
      letter-spacing: 3px; 
  }
}

@media (max-width: 768px) {
  .Hero {
      font-size: 1em;
      letter-spacing: 1px; 
  }
}
